div#mindmap {
  font-size: 14px;
  position: relative;
  display: flex;
}
div#mindmap div.rightClickTrigger {
  position: absolute;
  width: 100%;
  height: 100%;
}
div#mindmap div#dummy div {
  white-space: pre-wrap;
  width: max-content;
}
div#mindmap svg {
  flex: auto;
  outline: none;
  background-color: #eeeef3;
}
div#mindmap svg foreignObject {
  cursor: default;
  border-radius: 3px;
  border-width: 2px;
  border-color: transparent;
  border-style: solid;
}
div#mindmap svg foreignObject:focus {
  outline: none;
}
div#mindmap svg foreignObject div {
  text-align: left;
  border: 1px solid transparent;
  width: max-content;
  white-space: pre-wrap;
  color: #6d6d6d;
}
div#mindmap svg foreignObject div:focus {
  border-color: #9a9a9a;
  outline: none;
}
div#mindmap svg g.depth_0.node > foreignObject {
  background-color: white;
  border-radius: 5px;
}
div#mindmap svg g.depth_0.node > foreignObject div {
  color: #4b4b4b;
}
div#mindmap svg g.gButton {
  opacity: 0;
}
div#mindmap svg g.gButton > path {
  fill: #8685FF;
}
div#mindmap svg g.gButton > rect {
  fill: white;
  stroke: grey;
  stroke-width: 0.5;
}
div#mindmap svg.stroke-width-1 path {
  stroke-width: 1;
}
div#mindmap svg.stroke-width-2 path {
  stroke-width: 2;
}
div#mindmap svg.stroke-width-3 path {
  stroke-width: 3;
}
div#mindmap svg.stroke-width-4 path {
  stroke-width: 4;
}
div#mindmap svg.stroke-width-5 path {
  stroke-width: 5;
}
div#mindmap svg.stroke-width-6 path {
  stroke-width: 6;
}
div#mindmap svg.stroke-width-7 path {
  stroke-width: 7;
}
div#mindmap svg.stroke-width-8 path {
  stroke-width: 8;
}
div#mindmap svg.stroke-width-9 path {
  stroke-width: 9;
}
div#mindmap svg.stroke-width-10 path {
  stroke-width: 10;
}
div#mindmap svg path {
  fill: none;
  stroke-linecap: round;
}
div#mindmap svg #selectedNode:not(.depth_0) > foreignObject {
  background-color: rgba(0, 0, 255, 0.15);
}
div#mindmap svg #selectedNode.depth_0 > foreignObject {
  outline: 3px solid;
  outline-color: rgba(0, 0, 255, 0.15);
}
div#mindmap svg #newParentNode > foreignObject {
  border-color: rgba(0, 0, 255, 0.15);
}
div#mindmap svg #editing > foreignObject > div {
  background-color: white;
}
div#mindmap #menu {
  position: absolute;
  border-radius: 4px;
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  background-color: #FAFAFA;
  padding: 4px 0;
}
div#mindmap #menu:focus {
  outline: none;
}
div#mindmap #menu .menu-item {
  position: relative;
  padding: 4px 8px;
  cursor: pointer;
}
div#mindmap #menu .menu-item::before {
  background-color: black;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
div#mindmap #menu .menu-item:hover::before {
  opacity: 0.09;
}
div#mindmap div.button {
  position: absolute;
}
div#mindmap div.button.right-bottom {
  bottom: 0;
  right: 0;
}
div#mindmap div.button.top-right {
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
}
div#mindmap div.button button.icon {
  position: relative;
  cursor: pointer;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: 0;
  color: #3f51b5;
}
div#mindmap div.button button.icon::before {
  background-color: currentColor;
  border-radius: inherit;
  bottom: 0;
  color: inherit;
  content: "";
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
}
div#mindmap div.button button.icon:focus {
  outline: none;
}
div#mindmap div.button button.icon:hover::before {
  opacity: 0.1;
}
div#mindmap div.button button.icon.disabled {
  cursor: default;
}
div#mindmap div.button button.icon.disabled:hover::before {
  opacity: 0;
}
div#mindmap div.button button.icon.disabled i {
  filter: invert(85%) sepia(20%) saturate(0%) hue-rotate(125deg) brightness(86%) contrast(93%);
}
.wrapper {
  display: flex;
  margin: 8px;
  background-color: red;
}
.wrapper p {
  margin: 0;
}

body {
  color: #4c5566;
  font-family: sans-serif;
}

svg {
  font-size: 14px;
}

.tip {
  background-color: #eeeef3;
  height: 120px;
  width: 200px;
}
.tip text {
  fill: #4c5566;
}
.tip #hidden {
  visibility: hidden;
}

